import { isLoggiInitialized } from '../config';
import { _internalLog } from '../logger';
import { getRequestId } from '../next/request-context';

/**
 * Czy handlery sƒÖ ju≈º zarejestrowane
 */
let isRegistered = false;

/**
 * Rejestruje handlery dla nieobs≈Çu≈ºonych b≈Çƒôd√≥w i odrzuconych Promise'√≥w
 */
export function captureUnhandled(): void {
    if (isRegistered) return;
    // Browser lub Edge Runtime - pomijamy (brak process.on)
    if (typeof process === 'undefined' || typeof process.on !== 'function') return;

    isRegistered = true;

    // Uncaught exceptions
    process.on('uncaughtException', (error: Error) => {
        if (isLoggiInitialized()) {
            _internalLog('fatal', 'error', `Uncaught Exception: ${error.message}`, {
                error: error.message,
                stack: error.stack,
                requestId: getRequestId(),
            });
        }
        // Re-throw aby Node.js zako≈Ñczy≈Ç proces (domy≈õlne zachowanie)
        // W produkcji PM2/Docker powinien zrestartowaƒá proces
    });

    // Unhandled promise rejections
    process.on('unhandledRejection', (reason: unknown) => {
        if (isLoggiInitialized()) {
            const message = reason instanceof Error ? reason.message : String(reason);
            const stack = reason instanceof Error ? reason.stack : undefined;

            _internalLog('error', 'error', `Unhandled Promise Rejection: ${message}`, {
                error: message,
                stack,
                requestId: getRequestId(),
            });
        }
    });
}
