/**
 * Poziomy logowania
 * dev - logi developerskie z dodatkowym kontekstem (__file, __memory, __timestamp)
 * log - standardowe logowanie (dawny debug, odpowiednik console.log)
 */
export type LogLevel = 'dev' | 'log' | 'info' | 'warn' | 'error' | 'fatal';

/**
 * Kategorie log√≥w - u≈ºywane do grupowania i filtrowania
 * (bazowe + mo≈ºliwo≈õƒá rozszerzenia przez createLogger)
 */
export type LogCategory =
    | 'auth'       // Logowanie, sesje, OAuth
    | 'api'        // Wywo≈Çania API
    | 'security'   // Rate limiting, CSRF, walidacja
    | 'db'         // Operacje bazodanowe
    | 'middleware' // Middleware logi
    | 'console'    // Przechwycone z console.*
    | 'fetch'      // Przechwycone z fetch()
    | 'error'      // Uncaught errors
    | 'custom'     // W≈Çasne
    | 'flow'       // Przep≈Çyw operacji
    | string;      // Dynamiczne kategorie z API

/**
 * Definicja kategorii z API
 */
export interface CategorySchema {
    key: string;
    name: string;
    nameEn: string;
    icon: string;
    color: string;
    ansiColor: string;
    description: string;
    examples: string[];
    isBase: boolean;
}

/**
 * Schema logowania z API
 */
export interface LogSchema {
    projectId: string;
    projectName: string;
    categories: CategorySchema[];
    version: string;
    generatedAt: string;
}

/**
 * Environment aplikacji
 */
export type LogEnvironment = 'development' | 'production' | 'staging';

/**
 * ≈πr√≥d≈Ço loga
 */
export type LogSource = 'server' | 'client';

/**
 * Pojedynczy wpis loga
 */
export interface LogEntry {
    timestamp: string;
    level: LogLevel;
    category: LogCategory;
    source: LogSource;
    message: string;
    data?: Record<string, unknown>;
    requestId?: string;
    projectSlug?: string;
    environment?: LogEnvironment;

    // Kontekst HTTP (opcjonalny)
    requestUrl?: string;
    requestMethod?: string;
    requestStatus?: number;
    requestDurationMs?: number;

    // Kontekst b≈Çƒôdu (opcjonalny)
    error?: {
        name: string;
        message: string;
        stack?: string;
    };
}

/**
 * Konfiguracja SDK
 */
export interface LoggiConfig {
    /** Klucz API projektu w Loggi-App (opcjonalne - bez klucza dzia≈Ça offline mode) */
    apiKey?: string;

    /** Endpoint Loggi-App (domy≈õlnie: process.env.LOGGI_ENDPOINT lub http://localhost:3003) */
    endpoint?: string;

    /** Slug projektu (domy≈õlnie: process.env.LOGGI_PROJECT_SLUG lub auto z package.json) */
    projectSlug?: string;

    /** Environment (domy≈õlnie: process.env.NODE_ENV) */
    environment?: LogEnvironment;

    // Auto-capture
    /** Przechwytywanie console.* (domy≈õlnie: true) */
    captureConsole?: boolean;

    /** Przechwytywanie fetch() (domy≈õlnie: true) */
    captureFetch?: boolean;

    /** Przechwytywanie uncaught errors (domy≈õlnie: true) */
    captureUnhandled?: boolean;

    // Batching
    /** Wielko≈õƒá batcha (domy≈õlnie: 10) */
    batchSize?: number;

    /** Timeout batcha w ms (domy≈õlnie: 5000) */
    batchTimeoutMs?: number;

    // Dev mode
    /** Tryb debug - wiƒôcej log√≥w (domy≈õlnie: NODE_ENV === 'development') */
    debug?: boolean;

    /** Wy≈õwietlanie log√≥w w konsoli w dev mode (domy≈õlnie: true) */
    consoleInDev?: boolean;

    // Filtering
    /** Minimalny poziom log√≥w do wys≈Çania (domy≈õlnie: 'log' w dev, 'info' w prod) */
    minLevel?: LogLevel;

    /** Klucze do sanityzacji (domy≈õlnie: ['password', 'token', 'secret', ...]) */
    sensitiveKeys?: string[];

    /** Mapowanie prefix√≥w console.log na kategorie */
    prefixMap?: Record<string, LogCategory>;

    // Offline mode (internal - ustawiane automatycznie)
    /** Tryb offline - logi tylko do konsoli, bez wysy≈Çania do loggi-app */
    offlineMode?: boolean;
}

/**
 * Priorytet poziom√≥w logowania
 */
export const LEVEL_PRIORITY: Record<LogLevel, number> = {
    dev: 0,     // Najni≈ºszy - tylko development, z dodatkowym kontekstem
    log: 1,     // Standardowe logowanie (dawny debug)
    info: 2,
    warn: 3,
    error: 4,
    fatal: 5,
};

