import { LogEntry, LogEnvironment } from './types';
import { getConfig, isLoggiInitialized } from './config';

/**
 * Kolejka log√≥w do wys≈Çania
 */
let queue: LogEntry[] = [];

/**
 * Timer do automatycznego flush
 */
let flushTimer: ReturnType<typeof setTimeout> | null = null;

/**
 * Czy trwa shutdown
 */
let isShuttingDown = false;

/**
 * Czy jeste≈õmy w trybie offline (po nieudanych pr√≥bach)
 */
let isOffline = false;

/**
 * Licznik kolejnych b≈Çƒôd√≥w po≈ÇƒÖczenia
 */
let consecutiveFailures = 0;

/**
 * Max b≈Çƒôd√≥w przed przej≈õciem w tryb offline
 */
const MAX_FAILURES_BEFORE_OFFLINE = 3;

/**
 * Konfiguracja retry na starcie
 */
const STARTUP_RETRY_CONFIG = {
    maxRetries: 10,           // Max pr√≥b
    retryIntervalMs: 60000,   // Interwa≈Ç miƒôdzy pr√≥bami (1 minuta)
};

/**
 * Czy trwa retry na starcie
 */
let isRetrying = false;

/**
 * Licznik pr√≥b retry na starcie
 */
let startupRetryCount = 0;

/**
 * Timer do retry
 */
let retryTimer: ReturnType<typeof setTimeout> | null = null;

/**
 * Czy po≈ÇƒÖczenie zosta≈Ço nawiƒÖzane (przynajmniej raz)
 */
let connectionEstablished = false;

/**
 * Dodaj log do kolejki
 */
export function enqueue(entry: LogEntry): void {
    if (isShuttingDown) return;
    if (!isLoggiInitialized()) {
        // SDK nie zainicjalizowane - pomi≈Ñ
        return;
    }

    const config = getConfig();

    // Offline mode - nie kolejkuj, logi ju≈º pokazane w konsoli
    if (config.offlineMode || isOffline) {
        return;
    }

    queue.push(entry);

    // Nie flush gdy trwa retry - logi bƒôdƒÖ wys≈Çane po nawiƒÖzaniu po≈ÇƒÖczenia
    if (isRetrying) {
        return;
    }

    scheduleFlush();
}

/**
 * Zaplanuj flush kolejki
 */
function scheduleFlush(): void {
    const config = getConfig();

    if (config.offlineMode || isOffline) {
        return;
    }

    if (queue.length >= config.batchSize) {
        // Batch pe≈Çny - wy≈õlij od razu
        flush();
    } else if (!flushTimer) {
        // Zaplanuj flush po timeout
        flushTimer = setTimeout(() => {
            flushTimer = null;
            flush();
        }, config.batchTimeoutMs);
    }
}

/**
 * Wy≈õlij logi do Loggi-App
 */
export async function flush(): Promise<void> {
    if (queue.length === 0) return;
    if (!isLoggiInitialized()) return;

    const config = getConfig();

    // Offline mode - wyczy≈õƒá kolejkƒô (logi ju≈º by≈Çy w konsoli)
    if (config.offlineMode || isOffline) {
        queue = [];
        return;
    }

    const batch = queue.splice(0, config.batchSize);

    // Wyczy≈õƒá timer
    if (flushTimer) {
        clearTimeout(flushTimer);
        flushTimer = null;
    }

    try {
        // Endpoint bez /api/logs/collect - dodajemy tutaj
        const endpoint = config.endpoint.includes('/api/logs/collect')
            ? config.endpoint
            : config.endpoint + '/api/logs/collect';

        const response = await fetch(endpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'X-API-Key': config.apiKey,
            },
            body: JSON.stringify({
                logs: batch,
                projectSlug: config.projectSlug,
                environment: config.environment as LogEnvironment,
            }),
        });

        if (!response.ok) {
            throw new Error(`HTTP ${response.status}: ${response.statusText}`);
        }

        // Reset failure counter na sukces
        consecutiveFailures = 0;

        if (config.debug) {
            console.log(`[LOGGI] Sent ${batch.length} logs`);
        }
    } catch (error) {
        consecutiveFailures++;

        // Graceful degradation - po MAX_FAILURES przejd≈∫ w tryb offline
        if (consecutiveFailures >= MAX_FAILURES_BEFORE_OFFLINE) {
            isOffline = true;
            // Wyczy≈õƒá kolejkƒô - logi ju≈º by≈Çy w konsoli (je≈õli debug mode)
            queue = [];

            if (config.debug) {
                console.warn(`[LOGGI] Loggi-app unavailable after ${consecutiveFailures} failures. Switching to console-only mode.`);
            }
        } else if (config.debug) {
            console.warn(`[LOGGI] Failed to send logs (attempt ${consecutiveFailures}/${MAX_FAILURES_BEFORE_OFFLINE}):`, error);
        }
        // NIE retry - graceful degradation, odpuszczamy logi
    }

    // Je≈õli zosta≈Ço co≈õ w kolejce i nie jeste≈õmy offline, zaplanuj kolejny flush
    if (queue.length > 0 && !isOffline) {
        scheduleFlush();
    }
}

/**
 * Sprawd≈∫ czy transport jest w trybie offline
 */
export function isTransportOffline(): boolean {
    return isOffline;
}

/**
 * Resetuj tryb offline (np. po ponownym po≈ÇƒÖczeniu)
 */
export function resetOfflineMode(): void {
    isOffline = false;
    consecutiveFailures = 0;
    connectionEstablished = false;
    startupRetryCount = 0;
    isRetrying = false;
    if (retryTimer) {
        clearTimeout(retryTimer);
        retryTimer = null;
    }
}

/**
 * Sprawd≈∫ dostƒôpno≈õƒá serwera loggi-app
 */
async function checkServerAvailability(): Promise<boolean> {
    if (!isLoggiInitialized()) return false;

    const config = getConfig();
    if (config.offlineMode) return false;

    try {
        const healthEndpoint = config.endpoint + '/api/health';
        const response = await fetch(healthEndpoint, {
            method: 'GET',
            signal: AbortSignal.timeout(5000), // 5s timeout
        });
        return response.ok;
    } catch {
        return false;
    }
}

/**
 * Uruchom retry loop - pr√≥buje po≈ÇƒÖczyƒá siƒô z serwerem co minutƒô, max 10 razy
 */
async function startRetryLoop(): Promise<void> {
    if (isRetrying || connectionEstablished || isShuttingDown) return;
    if (!isLoggiInitialized()) return;

    const config = getConfig();
    if (config.offlineMode) return;

    isRetrying = true;
    startupRetryCount = 0;

    const attemptConnection = async () => {
        if (isShuttingDown || connectionEstablished) {
            isRetrying = false;
            return;
        }

        startupRetryCount++;

        if (config.debug) {
            console.log(`[LOGGI] Pr√≥ba po≈ÇƒÖczenia z serwerem log√≥w (${startupRetryCount}/${STARTUP_RETRY_CONFIG.maxRetries})...`);
        }

        const available = await checkServerAvailability();

        if (available) {
            connectionEstablished = true;
            isRetrying = false;
            isOffline = false;
            consecutiveFailures = 0;

            if (config.debug) {
                console.log('[LOGGI] ‚úÖ Po≈ÇƒÖczono z serwerem log√≥w');
            }

            // Wy≈õlij zaleg≈Çe logi
            if (queue.length > 0) {
                scheduleFlush();
            }
            return;
        }

        if (startupRetryCount >= STARTUP_RETRY_CONFIG.maxRetries) {
            // Wyczerpano pr√≥by - przejd≈∫ w tryb offline na sta≈Çe
            isRetrying = false;
            isOffline = true;
            queue = []; // Wyczy≈õƒá kolejkƒô

            console.warn(
                `[LOGGI] ‚ùå Nie uda≈Ço siƒô po≈ÇƒÖczyƒá z serwerem log√≥w po ${STARTUP_RETRY_CONFIG.maxRetries} pr√≥bach. ` +
                `Przechodzƒô w tryb offline (tylko konsola).`
            );
            return;
        }

        // Zaplanuj nastƒôpnƒÖ pr√≥bƒô
        if (config.debug) {
            console.log(`[LOGGI] Serwer niedostƒôpny. Nastƒôpna pr√≥ba za ${STARTUP_RETRY_CONFIG.retryIntervalMs / 1000}s...`);
        }

        retryTimer = setTimeout(attemptConnection, STARTUP_RETRY_CONFIG.retryIntervalMs);
    };

    // Pierwsza pr√≥ba natychmiast
    await attemptConnection();
}

/**
 * Inicjalizuj transport - wywo≈Çaj po initLoggi()
 */
export async function initTransport(): Promise<void> {
    if (!isLoggiInitialized()) return;

    const config = getConfig();
    if (config.offlineMode) return;

    // Sprawd≈∫ czy serwer jest dostƒôpny
    const available = await checkServerAvailability();

    if (available) {
        connectionEstablished = true;
        if (config.debug) {
            console.log('[LOGGI] ‚úÖ Serwer log√≥w dostƒôpny');
        }
    } else {
        // Serwer niedostƒôpny - uruchom retry loop w tle
        console.warn('[LOGGI] ‚ö†Ô∏è Serwer log√≥w niedostƒôpny. Uruchamiam retry w tle...');
        startRetryLoop();
    }
}

/**
 * Graceful shutdown - wy≈õlij pozosta≈Çe logi przed zamkniƒôciem
 */
async function gracefulShutdown(signal: string): Promise<void> {
    if (isShuttingDown) return;
    if (!isLoggiInitialized()) return; // SDK nie zainicjalizowane - nic do zrobienia
    isShuttingDown = true;

    const config = getConfig();
    if (config.debug) {
        console.log(`[LOGGI] Received ${signal}, flushing remaining logs...`);
    }

    // Wyczy≈õƒá timery
    if (flushTimer) {
        clearTimeout(flushTimer);
        flushTimer = null;
    }
    if (retryTimer) {
        clearTimeout(retryTimer);
        retryTimer = null;
    }
    isRetrying = false;

    // Wy≈õlij wszystko co zosta≈Ço (je≈õli nie jeste≈õmy offline i mamy po≈ÇƒÖczenie)
    if (!config.offlineMode && !isOffline && connectionEstablished && queue.length > 0) {
        await flush();
    }

    if (config.debug) {
        console.log('[LOGGI] Shutdown complete');
    }
}

// Rejestruj handlery shutdown (tylko w Node.js, nie w Edge Runtime)
// Edge Runtime ma `process` ale nie ma `process.on`
if (typeof process !== 'undefined' && typeof process.on === 'function') {
    process.on('SIGINT', () => gracefulShutdown('SIGINT'));
    process.on('SIGTERM', () => gracefulShutdown('SIGTERM'));
    process.on('beforeExit', () => gracefulShutdown('beforeExit'));
}
